import React ,{ Component }from 'react';//,
import { Tree } from 'antd';
import { BlockOutlined } from '@ant-design/icons';
const { TreeNode } = Tree;
/*****************************ant**********************************
 *1.
 *2.
 */

//
let treeData = [
    {//
        title: '', 
        key:"1",
        checkable:false,
        name:"'1",
        children:[
            {
                title: '1.1',
                key: '1.1',
                children: [
                  { title: '1.1.1', key: '1.1.1',checkable:true,icon: <BlockOutlined />},
                  { title: '1.1.2', key: '1.1.2' ,icon: <BlockOutlined />},
                  { title: '1.1.3', key: '1.1.3' ,icon: <BlockOutlined />},
                ],
            },
            {
                title: '1.2',
                key: '1.2',
                name:"'1.2",
                children:[

                ]
            },
        ]  
    },
];

class ant_tree extends Component{
    constructor(props){
        super(props);
        this.state={
            name:"ant",
            showLine:false,//
            autoExpandParent:true,//
            draggable:true,//
            checkable:true,//
            showIcon:true,//false
            expandedKeys: ["1"],//
            checkedKeys: ["1.1.2"],//
            selectedKeys: [],//
            treeData:treeData
        }
    }
    ///
    onExpand =(expandedKeys,obj) => {
        console.log('onExpand', expandedKeys);
        this.setState({
            expandedKeys:expandedKeys,
            autoExpandParent:false,
        });
    }
    /*******************************
     *:2020.08.12
     *:param(*):
     *****checkedKeys(array):checkboxkey,:["1","1.5"]
     ******evt(Event):checkbox
     *:
     *1.,
     ***/
    onCheck=(param,checkedKeys,evt) => {
        let nodeProps=evt.node?evt.node.props:{};//Check
        let checkStatus=nodeProps.checked==false?true:false;//checkbox
        console.log('onCheck', checkedKeys);
        console.log('nodeProps', nodeProps);
        this.setState({ checkedKeys });
    }//e

    //
    onSelect = (selectedKeys,info) => {
        console.log('onSelect', info);
        this.setState({ selectedKeys });
    }//e
    //
    onDropFun=(info,node)=>{
        console.log(info);
        const dropKey = info.node.key;//key
        const dragKey = info.dragNode.key;//key
        const dropPos = info.node.pos.split('-');
        const dropPosition = info.dropPosition - Number(dropPos[dropPos.length - 1]);
        const loop = (data, key, callback) => {
            for (let i = 0; i < data.length; i++) {
              if (data[i].key === key) {
                return callback(data[i], i, data);
              }
              if (data[i].children) {
                 loop(data[i].children, key, callback);
              }
            }
        };
        const data = [...this.state.treeData];//
        let dragObj;
        //
        loop(data,dragKey,(item, index, arr) => {
            arr.splice(index,1);
            dragObj = item;
        });
        if (!info.dropToGap) {
            // Drop on the content
            loop(data, dropKey, item => {
              item.children = item.children || [];
              // where to insert 
              item.children.push(dragObj);
            });
        } 
        else if (
            (info.node.children || []).length > 0 && // Has children
            info.node.expanded && // Is expanded
            dropPosition === 1 // On the bottom gap
          ) {
            loop(data, dropKey, item => {
              item.children = item.children || [];
              // where to insert 
              item.children.unshift(dragObj);
            });
        } 
        else {
            let ar;
            let i;
            loop(data, dropKey, (item, index, arr) => {
              ar = arr;
              i = index;
            });
            if (dropPosition === -1) {
              ar.splice(i, 0, dragObj);
            }
            else {
              ar.splice(i + 1, 0, dragObj);
            }
        }
        this.setState({treeData: data});//
    }//e

    render(){
        return(
            <div className="wrap">
                <h2>{this.state.name}</h2>
                <Tree
                    style={{border:"1px solid red",width:"300px"}}
                    checkedKeys={this.state.checkedKeys}//checkboxkey
                    checkable={this.state.checkable}
                    showLine={this.state.showLine}
                    draggable={this.state.draggable}
                    showIcon={this.state.showIcon}
                    autoExpandParent={this.state.autoExpandParent}
                    expandedKeys={this.state.expandedKeys}
                    treeData={this.state.treeData}
                    onExpand={this.onExpand}
                    onCheck={this.onCheck.bind(this,"ddd")}
                    onSelect={this.onSelect}
                    onDrop={this.onDropFun}
                />
                <hr/>
                <button onClick={this.addTreesj}></button>
            </div>
        )
    }
    componentDidMount(){
        
    }
    //
    addTreesj=()=>{
        this.setState({
            treeData:treeData,
            expandedKeys:["1","1.1"],
        })
    }
}
export default ant_tree;