import React,{ Component }from 'react';
import * as api from '../../api/api';
import { Table, Divider, Tag} from 'antd';
import { Button } from 'antd';


/********************table*********************
 *1.:
 *2. 
 */
class ant_table_ht extends Component{
    constructor(props){
        super(props);
        this.state={
            name:"ant_table()",
            data:[],//
            columns:[],//
            pagination:this.pagination,//
            selectedRowKeys:[],//key
        }
    }
    //
    table_columns=[
        {
            dataIndex:"key",//
            title:"",//
            align:"center",//
        },
        {
            dataIndex:'tbr',
            title:'',
            align:"center",//
        },
        {
            dataIndex: 'jlsj',
            title: '',
            align:"center",//
        },
        {
            dataIndex: 'szdq',
            title: '',
            align:"center",//
        },
        //
        {
            title:"",
            align:"center",//
            render:(row)=>{//(this)
                let rowStyle=row.age>40?{"color":"red"}:{"color":"blue"};
                return(
                    <span>
                        <a id="ck" style={rowStyle}></a>
                        <Divider type="vertical" />{/*  */}
                        <a id="sc" style={rowStyle}></a>
                    </span>
                )
            }
        },
    ]
    //checkbox
    row_checkbox={
        onChange:(selectedRowKeys,selectedRows)=>{//checkbox
          this.setState({ selectedRowKeys });
          console.log("**********Ids***********",selectedRowKeys);
          console.log("**********rows***********",selectedRows);
        },
        getCheckboxProps:(row)=>{//checkbox(checkbox)
           return ({
             disabled:row.isDisabled==true?true:false,
          })
        },
        selectedRowKeys:[],//talble checkboxkey
    };
    //pagination
    pagination={
        position:"bottom",//
        defaultCurrent:1,//,1
        defaultPageSize:5,//,10
        simple:true,//,,:"1/10"false
        hideOnSinglePage:true,//,false
        //pageSize:10,//(),"pageSize""defaultPageSize"
        //current:1,//(),"current""defaultPageSize"
        //pageSizeOptions:['10','20','30','40'],//,showSizeChanger
        showSizeChanger:true,// pageSize,pageSizeOptions
        showQuickJumper:true,//
        // showTotal:(total,range)=>{//,
        //     //return range[0]+"~"+range[1]+"/"+total;
        // },
        // itemRender:(current,type,originalElement)=>{//
        //     if(type==='prev'){
        //         return <a></a>;
        //     }else if(type==='next') {
        //         return <a></a>;
        //     }
        //     return originalElement;
        // },
    };
    render(){
        console.log("*******render()******");
        this.row_checkbox.selectedRowKeys=this.state.selectedRowKeys;
        return(
            <div>
                <h2>{this.state.name}</h2>
                <input ref="input_val"></input>
                <Button type="primary" onClick={this.setCurrentPage.bind(this)}></Button>
                <Button type="primary" onClick={this.clearTable.bind(this)}></Button>
                <Table 
                    rowSelection={this.row_checkbox}
                    columns={this.state.columns} 
                    dataSource={this.state.data}
                    bordered={true} 
                    size="middle"
                    pagination={this.state.pagination}
                    onChange={this.handleTableChange}
                />
            </div>
        )
    }
    //
    handleTableChange=(pagination,filters,sorter) => {
        let pageSize=pagination.pageSize;//
        let currentPage=pagination.current;//
        this.setState({pagination},()=>{this.addTableData();});
    };
    //
    setCurrentPage(){
        var currentPage=this.refs.input_val.value||"";
        if(!currentPage)return false;
        this.state.pagination.current=eval(currentPage);
        this.setState({pagination:this.state.pagination},()=>{this.addTableData();});
    }
    componentDidMount(){
        this.addTableData();//
     }
    //ajax
    addTableData(){
        let _self=this;
        this.setState({ loading: true });//
        //ajax
        var url="http://localhost:9000/static/datas/table_data.json";
        var param={
            pageSize:this.state.pagination.pageSize||this.state.pagination.defaultPageSize,
            currentPage:this.state.pagination.current||this.state.pagination.defaultCurrent,
        };
        api.get(url,param,(result) =>{
            console.log(result);
            _self.setState({
              loading:false,//
              columns:_self.table_columns,//
              data:result.returnData.rows||[],//
              selectedRowKeys:[]//key
            });
        });
    }
    //
    clearTable(){
        this.setState({
            data:[],//
            selectedRowKeys:[]//key
        });
    }
}
export default ant_table_ht;