axios.defaults.headers.post['Content-Type']='application/json;charset=UTF-8';//
//postheadersparam:"application/x-www-form-urlencoded"
//axios.defaults.headers.post['Content-Type']='application/x-www-form-urlencoded;charset=UTF-8';
axios.defaults.withCredentials=true;//cookie
axios.defaults.timeout=5000// 5000ms
//console.log(axios.defaults);



/**
 * Get
 * @param url
 * @param param
 * @returns {AxiosPromise<any>}
 * @constructor
 */
export function get(url, param,callback) {
    axios.get(url,{
        params: param,
        headers: {
            //token
            token:window.sessionStorage.getItem("myToken")||"",
        }
    }).then(function(response){
        if(response.status==200){
            let result={
                status:response.status,//
                param:response.config.params||{},//
                //text:response.request.responseText,
                data:response.data,
            };
            callback(result);
        }
    })
}//e

//get promise
export function get_promise(url,param){
    let promise=axios.get(url,{
        params: param,
        headers: {
            //token
            token:window.sessionStorage.getItem("myToken")||"",
        }
    });
    return promise;
}//e

/**
 * Post
 * @param url
 * @param param
 * @returns {AxiosPromise<any>}
 * @constructor
 */
export function post(url,param,callback){
    param=param||{};
    axios.post(url,param,{
        headers: {
            //token
            token:window.sessionStorage.getItem("myToken")||"",
        }
    }).then(function(response){
        if(response.status==200){
            let param=response.config.data||{};
            if(typeof(param)=="string")param=JSON.parse(response.config.data)
            let result={
                param:param,
                text:response.request.responseText,
                data:response.data,
                status:response.status
            };
            callback(result);
        }
    })
}

/**
 * Delete
 * @param url
 * @param par
 * @returns {AxiosPromise}
 * @constructor
 */
export function del(url, param) {
    return axios.delete(url, {
        params: param,
        headers: {
            //  token
        }
    })
}

/**
 * Put
 * @param url
 * @param par
 * @returns {AxiosPromise<any>}
 * @constructor
 */
export function put(url, param) {
    return axios.put(url, param, {
        headers: {
            //  token
        }
    })
}