import React ,{ Component }from 'react';
import { Divider } from 'antd';
import { Checkbox } from 'antd';
/*****************************ant_form_Checkbox**********************************
 *1.,"this"""
 */
class ant_form_Checkbox extends Component{
    constructor(props){
        super(props);
        this.state={
            name:"ant_form_Checkbox",
            options:[//checkbox
                { label: '', value: '1' },
                { label: '', value: '2' },
                { label: '', value: '3' },
            ],
            checkedValues:["2","3"],//checkbox
        }
    }
    //()
    onChange=(checkedValues)=>{
        console.log('checked = ',checkedValues);//["2", "3"]
        this.setState({checkedValues});
    }

    render(){
        return(
            <div className="wrap">
                <h2>{this.state.name}</h2><Divider/>
                {/* 1.Checkbox  */}
                <Checkbox.Group 
                    options={this.state.options} 
                    value={this.state.checkedValues} 
                    onChange={this.onChange} 
                />
            </div>
        )
    }
    componentDidMount(){
          
    }
}
export default ant_form_Checkbox;
