import React ,{ Component }from 'react';
import { Anchor } from 'antd';
import { Card, Col, Row } from 'antd';
const { Link } = Anchor;

class index extends Component{
    constructor(props){
        super(props);
        this.state={
            name:"index",
        }
    }
    render(){
        return(
            <div className="wrap">
                <Row gutter={16}>
                <Col span={6}>
                    <Card title="" bordered={false}>    
                
                        {/*        */}
                        <Anchor onClick={e => e.preventDefault()} getContainer={() => document.getElementById('content')}  >
                            <Link href="#First"  title=""  />
                            <Link href="#Second" title="" />
                        </Anchor>
                    </Card>
                </Col>
                <Col span={4}  >
                    <Card title="" bordered={false} >
                        //   
                        <div  id='content' style={{height:100,overflow:'auto'}}> // 
                            <h1 id="First"></h1>
                            <span>
                            JavaScript,Web,,JavaScriptHTML
                            
                            HTML
                            HTMLjs
                            WindowsLinuxMacAndroidiOS
                            JavascriptJavascript
                        </span>
                            <h1 id="Second"></h1>
                            <span>
                            NetscapeBrendan EichJavaScriptEcmaJavaScriptECMAScriptJavaScriptJavaScriptECMAScript [7]
                            NetscapeLiveScriptNetscapeSunJavaScriptJavaScriptJavaJavaJavaJavaScriptSelfSchemeJavaScriptJavaNetscapeSunJScriptJavaScriptEcmaECMA-262ECMAScriptECMAScriptJavaScriptJavaScript [8]
                            JavaScriptNetscapeJavaScriptJScriptCEnviScriptEase1997ECMANetscapeSunBorlandECMA-262
                            </span>
                        </div>
                    </Card>
                </Col>
                </Row>
            </div>
        )
    }
    componentDidMount(){
          
    }
}
export default index;
