import React,{ Component }from 'react';

import { Table, Divider, Tag} from 'antd';
import { Button } from 'antd';
const { Column, ColumnGroup } = Table;

/********************ant_table*********************
 *1. 
 */
class ant_table extends Component{
    constructor(props){
        super(props);
        this.state={
            name:"ant-table",
            data:[],//
            columns:[],//
            selectedRowKeys:["2"],//key
            type:"1",//table
        }
    }
    //
    table_columns=[
        {
            dataIndex:"key",//
            title:"",//
            align:"center",//
        },
        {
            dataIndex: 'name',
            title:'',
            align:"center",//
        },
        {
            dataIndex: 'age',
            title: '',
            align:"center",//
        },
        {
            dataIndex: 'address',
            title: '',
            align:"center",//
        },
        //
        {
            title:"",
            align:"center",//
            render:(row)=>{//(this)
                let rowStyle=row.age>40?{"color":"red"}:{"color":"blue"};
                return(
                    <span>
                        <a id="ck" style={rowStyle}></a>
                        <Divider type="vertical" />{/*  */}
                        <a id="sc" style={rowStyle}></a>
                    </span>
                )
            }
        },
    ]
    //
    table_data=[
        {
            key:"1",//key,
            name: '',
            age: 32,
            address: '1',
        },
        {
            key:'2',
            name: '',
            age: 42,
            address: '1',
        },
        {
            key:'3',
            name: '',
            age: 55,
            address: '1',
            isDisabled:true
        },
    ]
    //checkbox
    row_checkbox={
        onChange:(selectedRowKeys,selectedRows)=>{//checkbox
          this.setState({ selectedRowKeys });
          console.log("**********Ids***********",selectedRowKeys);
          console.log("**********rows***********",selectedRows);
        },
        getCheckboxProps:(row)=>{//checkbox(checkbox)
           return ({
             disabled:row.isDisabled==true?true:false,
          })
        },
        selectedRowKeys:[],//talble checkboxkey
    };
    render(){
        console.log("*******render()******");
        this.row_checkbox.selectedRowKeys=this.state.selectedRowKeys;
        return(
            <div>
                <h2>{this.state.name}</h2>
                <Button type="primary" onClick={this.addTableData.bind(this)}></Button>
                <Button type="primary" onClick={this.clearTable.bind(this)}></Button>
                {this.state.type=="2"?this.table_columns_method2():this.table_columns_method1()}
            </div>
        )
    }
    //1.Tablecolumns()
    table_columns_method1(){
        return (
            <Table 
                rowSelection={this.row_checkbox}
                columns={this.state.columns} 
                dataSource={this.state.data}
                bordered={true} 
            />
        );
    }
    //1.Tablecolumns()
    table_columns_method2(){
        return (
            <Table dataSource={this.state.data}>
                <Column title="" dataIndex="key"/>
                <Column title="" dataIndex="name"/>
                <Column title="" dataIndex="age"/>
                <Column title="" dataIndex="address"/>
                <Column title="" render={this.formatOperate.bind(this)}/>
            </Table>
        );
    }
    //(this)
    formatOperate(row){
        let rowStyle=row.age>40?{"color":"red"}:{"color":"blue"};
        return(
            <span>
                <a id="ck" style={rowStyle}></a>
                <Divider type="vertical" />{/*  */}
                <a id="sc" style={rowStyle}></a>
            </span>
        )
    }
    componentDidMount(){
        this.setState({
            data:this.table_data,//
            columns:this.table_columns//
        });
    }
    //
    addTableData(){
        //
        const data = [];
        for(let i = 0; i < 46; i++){
            data.push({
                key:i+1,
                name:`Edward King ${i}`,
                age: 32,
                address: `London, Park Lane no. ${i}`,
            });
        }
        this.setState({
            data:data,//
            columns:this.table_columns,
            selectedRowKeys:[]//key
        });
    }
    //
    clearTable(){
        this.setState({
            data:[]
        });
    }
}
export default ant_table;