import React ,{ Component }from 'react';
import { Divider } from 'antd';
import Icon from '@ant-design/icons';
import { createFromIconfontCN } from '@ant-design/icons';
import { 
    StarOutlined, 
    StarFilled, 
    StarTwoTone, 
    HomeOutlined
} 
from '@ant-design/icons';//

//1.svg
import { ReactComponent as layer } from '../antDesign/icons/svg/layer.svg';

//2.font-class"fonticon"(,Unexpected token '<')
//import './icons/iconfont/test1/iconfont.css';

//IconFont
// const MyIcon= createFromIconfontCN({
//     scriptUrl: '//at.alicdn.com/t/font_1997618_40znixucugd.js',//fonticon.js
// });

//IconFont
const MyIcon= createFromIconfontCN({
    //scriptUrl:'./icons/iconfont/script/test2.js',//fonticon.js()
    scriptUrl:'./static/iconfont/script/iconfont_tool.js',//webfonticon.js()
});


/*****************************Icon**********************************
 *1.Icon <svg>  style  className 
 *2.Iconsvg
 *3.anticonfontL
    1)iconfont
        scriptUrl,:"//at.alicdn.com/t/font_1997618_40znixucugd.js"
    2)iconfont
        scriptUrlwebiconfont.js,
    es6
 */
class ant_icon extends Component{
    constructor(props){
        super(props);
        this.state={
            name:"Icon",
        }
    }
    render(){
        return(
            <div className="wrap">
                <h2>{this.state.name}</h2>
                
                {/************1.ant icon***********/}
                <Divider orientation="left">1.ant icon</Divider>
                <StarOutlined style={{ fontSize: '16px', color: 'red' }}/><p/>
                <HomeOutlined 
                   spin={false}
                /><p/>
                
                {/************2.svg***********/}
                <Divider orientation="left">2.svg</Divider>
                <Icon component={layer} style={{ fontSize: '16px', color: '#08c' }}/>
                
                {/************3.iconfont(Symbol,)***********/}
                <Divider orientation="left">3.iconfont</Divider>
                <MyIcon type="iconyuandian1" style={{ fontSize: '20px', color: 'blue' }}/>
                
                {/************4.iconfont(font-class)***********/}
                <Divider orientation="left">4.iconfont(font-class)</Divider>
                <i className="iconfont iconzanwu"></i>
            </div>
        )
    }
    componentDidMount(){
          
    }
}
export default ant_icon;
